<template>
  <div class="brokerage">
    <shop-form ref="form" :model="form" :rules="rules" class="c_content">
      <shop-item label="提取金额(可提取888)" prop="amount">
        <template slot="valid" slot-scope="{ valid }">
          <shop-input v-model="form.amount" placeholder="请输入提取金额" :state="valid" :max="'888'" />
        </template>
      </shop-item>
      <shop-item label="到账银行" prop="cid">
        <template slot="valid" slot-scope="{ valid }">
          <div :style="{width:'100%',height:'auto'}" @click="path('bankcard')">
            <shop-input
              v-model="form.cid"
              rightIcon="iconyou"
              :state="valid"
              placeholder="请选择银行卡"
              readonly
            />
          </div>
        </template>
      </shop-item>
      <div class="footer">
        <button type="submit" @click.prevent="submitForm">确定</button>
      </div>
    </shop-form>
  </div>
</template>
<script>
import { isInt } from "@/utils/validate";
export default {
  data() {
    const validateAmount = (rule, value, callback) => {
      if (!isInt(value)) {
        callback(new Error("请输入正数"));
      } else {
        callback();
      }
    };
    return {
      value: "",
      form: {
        amount: "",
        cid: "",
      },
      rules: {
        amount: [
          { required: true, trigger: "blur", validator: validateAmount },
        ],
        cid: [{ required: true, trigger: "blur" }],
      },
    };
  },
  methods: {
    path(name) {
      this.$router.push({ name });
    },
    submitForm() {
      this.$refs["form"].validate((valid) => {
        if (valid) {
          console.log(1);
        }
      });
    },
  },
};
</script>
<style lang="scss" scoped>
.brokerage {
  width: 100%;
  height: 100vh;
  background-color: #ffffff;
  .c_content {
    height: 100%;
    padding: 12px 20px;
  }
}
</style>



